import React from 'react';
import { useHistory } from 'react-router-dom';
import {
  PersonBg,
  AvatarBox,
  PersonInner,
  Avatar,
  PersonBgInner,
  NickBox,
  NickName,
  Sign,
  FucList,
  FucListItem,
  FucListItemTop,
  FucListItemBottom,
  MenuBox,
  MenuItem,
  MenuLabel,
  MenuIcon,
  MsgNum,
} from './styles';
import iconPersonal from './images/icon-personal.svg';
import iconMsg from './images/icon-msg.svg';
import iconSetting from './images/icon-setting.svg';

const UserCenter: React.FC = () => {
  const history = useHistory();
  const handleMenu = (type: string): void => {
    switch (type) {
      case 'msg':
        history.push('/msg');
        break;
      case 'personal':
        history.push('/complete-all');
        break;
      case 'setting':
        history.push('/setting');
        break;
      default:
    }
  };
  return (
    <>
      <PersonBg>
        <PersonBgInner />
        <PersonInner>
          <AvatarBox>
            <Avatar />
          </AvatarBox>
          <NickBox>
            <NickName>孜然肉片</NickName>
            <Sign>XXX</Sign>
          </NickBox>
        </PersonInner>
      </PersonBg>
      <FucList>
        <FucListItem>
          <FucListItemTop />
          <FucListItemBottom>练习记录</FucListItemBottom>
        </FucListItem>
        <FucListItem>
          <FucListItemTop />
          <FucListItemBottom>练习记录</FucListItemBottom>
        </FucListItem>
        <FucListItem>
          <FucListItemTop />
          <FucListItemBottom>练习记录</FucListItemBottom>
        </FucListItem>
        <FucListItem>
          <FucListItemTop />
          <FucListItemBottom>练习记录</FucListItemBottom>
        </FucListItem>
      </FucList>
      <MenuBox>
        <MenuItem onClick={(): void => handleMenu('personal')}>
          <MenuLabel>
            <MenuIcon src={iconPersonal} />
            个人信息
          </MenuLabel>
        </MenuItem>
        <MenuItem onClick={(): void => handleMenu('msg')}>
          <MenuLabel>
            <MenuIcon src={iconMsg} />
            消息中心
          </MenuLabel>
          <MsgNum>18</MsgNum>
        </MenuItem>
        <MenuItem onClick={(): void => handleMenu('setting')}>
          <MenuLabel>
            <MenuIcon src={iconSetting} />
            通用设定
          </MenuLabel>
        </MenuItem>
      </MenuBox>
    </>
  );
};

export default UserCenter;
