import React, { useContext, useState } from 'react';
import { Accordion, Picker, WhiteSpace, Button } from 'antd-mobile';
import PickItem from '@/components/PickItem';
import { useHistory } from 'react-router-dom';
import { ejuSubject } from '@/constants/globalDatas';
import { Context } from '@/reducer/context';
import {
  AccordionBox,
  AccordionItem,
  AccordionLeft,
  AccordionLeftStarBox,
  AccordionLeftTite,
  AccordionRight,
  Content,
  Star,
  StarBox,
  StarFont,
  TabBarBox,
  TabBarItem,
} from './styles';

const Library: React.FC = () => {
  // const [tabType, setTabType] = useState('0'); // 标签类型
  const [ejuSubjectValue, setEjuSubjectValue] = useState<string[]>([]); // 科目
  const { state, dispatch } = useContext(Context);
  const { tabType } = state;
  const history = useHistory();
  // 切换标签
  const handleTabChange = (type: string): void => {
    // setTabType(type);
    dispatch({ type: 'setTabType', tabType: type });
  };
  return (
    <Content>
      <TabBarBox>
        <TabBarItem
          className={tabType === '0' ? 'active' : ''}
          onClick={(): void => handleTabChange('0')}
        >
          智能速答
        </TabBarItem>
        <TabBarItem
          className={tabType === '1' ? 'active' : ''}
          onClick={(): void => handleTabChange('1')}
        >
          专题模拟
        </TabBarItem>
        <TabBarItem
          className={tabType === '2' ? 'active' : ''}
          onClick={(): void => handleTabChange('2')}
        >
          套卷模拟
        </TabBarItem>
      </TabBarBox>
      <WhiteSpace size="xl" />

      <Picker
        data={ejuSubject}
        cols={1}
        value={ejuSubjectValue}
        onOk={(value: string[]): void => setEjuSubjectValue(value)}
      >
        <PickItem placeholder="请选择考学科目" />
      </Picker>
      {tabType === '0' && (
        <>
          <Accordion defaultActiveKey="0">
            <Accordion.Panel header="1 微积分">
              <AccordionBox>
                <AccordionItem>
                  <AccordionLeft>
                    <AccordionLeftTite>1. XXXXX</AccordionLeftTite>
                    <AccordionLeftStarBox>
                      <StarFont>菓星值</StarFont>
                      <StarBox>
                        <Star />
                        <Star />
                        <Star />
                        <Star />
                        <Star />
                      </StarBox>
                    </AccordionLeftStarBox>
                  </AccordionLeft>
                  <AccordionRight>
                    <Button
                      type="primary"
                      size="small"
                      inline
                      onClick={(): void => history.push('/quick')}
                    >
                      开始练习
                    </Button>
                  </AccordionRight>
                </AccordionItem>
                <AccordionItem>
                  <AccordionLeft>
                    <AccordionLeftTite>1. XXXXX</AccordionLeftTite>
                    <AccordionLeftStarBox>
                      <StarFont>菓星值</StarFont>
                      <StarBox>
                        <Star className="active" />
                        <Star className="active" />
                        <Star className="active" />
                        <Star />
                        <Star />
                      </StarBox>
                    </AccordionLeftStarBox>
                  </AccordionLeft>
                  <AccordionRight>
                    <Button
                      type="primary"
                      size="small"
                      inline
                      onClick={(): void => history.push('/quick')}
                    >
                      开始练习
                    </Button>
                  </AccordionRight>
                </AccordionItem>
                <AccordionItem>
                  <AccordionLeft>
                    <AccordionLeftTite>1. XXXXX</AccordionLeftTite>
                    <AccordionLeftStarBox>
                      <StarFont>菓星值</StarFont>
                      <StarBox>
                        <Star className="all" />
                        <Star className="all" />
                        <Star className="all" />
                        <Star className="all" />
                        <Star className="all" />
                      </StarBox>
                    </AccordionLeftStarBox>
                  </AccordionLeft>
                  <AccordionRight>
                    <Button
                      type="primary"
                      size="small"
                      inline
                      onClick={(): void => history.push('/quick')}
                    >
                      开始练习
                    </Button>
                  </AccordionRight>
                </AccordionItem>
              </AccordionBox>
            </Accordion.Panel>
          </Accordion>
        </>
      )}
      {tabType === '1' && (
        <>
          <AccordionItem>
            <AccordionLeft>
              <AccordionLeftTite>1. 微积分</AccordionLeftTite>
              <AccordionLeftStarBox>
                <StarFont>菓星值</StarFont>
                <StarBox>
                  <Star className="active" />
                  <Star className="active" />
                  <Star className="active" />
                  <Star />
                  <Star />
                </StarBox>
              </AccordionLeftStarBox>
            </AccordionLeft>
            <AccordionRight>
              <Button
                type="primary"
                size="small"
                inline
                onClick={(): void => history.push('/topic')}
              >
                开始练习
              </Button>
            </AccordionRight>
          </AccordionItem>
        </>
      )}
      {tabType === '2' && (
        <>
          <Accordion defaultActiveKey="0">
            <Accordion.Panel header="中心考试（日本高考）">
              <AccordionBox>
                <AccordionItem>
                  <AccordionLeft>
                    <AccordionLeftTite>2021年6月</AccordionLeftTite>
                    <AccordionLeftStarBox>
                      <StarFont>个人历史最高分：150点</StarFont>
                    </AccordionLeftStarBox>
                  </AccordionLeft>
                  <AccordionRight>
                    <Button
                      type="primary"
                      size="small"
                      inline
                      onClick={(): void => history.push('/exam-info')}
                    >
                      开始练习
                    </Button>
                  </AccordionRight>
                </AccordionItem>
              </AccordionBox>
            </Accordion.Panel>
          </Accordion>
        </>
      )}
    </Content>
  );
};

export default Library;
