import styled from 'styled-px2vw';
import bg from './images/bg.png';
import pkg from '../../../package.json';

const themeColor = pkg.theme['brand-primary'];

export const PersonBg = styled.div`
  height: 312px;
  position: relative;
`;
export const PersonInner = styled.div`
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
`;
export const PersonBgInner = styled(PersonInner)`
  z-index: 1;
  overflow: hidden;
  &::before {
    content: '';
    position: absolute;
    background: url(${bg}) no-repeat center;
    filter: blur(10px);
    background-size: cover;
    width: 115%;
    height: 115%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
`;
export const AvatarBox = styled.div`
  width: 220px;
  height: 220px;
  border-radius: 50%;
  background-color: #fff;
  padding: 12px;
  position: absolute;
  left: 42px;
  bottom: -14px;
  box-shadow: 0px 4px 20px rgba(101, 101, 101, 0.15);
`;
export const Avatar = styled.div`
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: url(${bg}) no-repeat center;
  background-size: cover;
`;

export const NickBox = styled.div`
  width: 375px;
  height: 206px;
  position: absolute;
  right: 0;
  bottom: 0;
  color: #fff;
`;

export const NickName = styled.div`
  font-weight: bold;
  font-size: 38px;
`;
export const Sign = styled.div`
  font-size: 22px;
  margin-top: 70px;
`;

export const FucList = styled.div`
  height: 366px;
  background: #f5f5f5;
  display: flex;
  align-items: center;
  width: 100%;
  overflow: auto;
`;

export const FucListItem = styled.div`
  width: 284px;
  height: 234px;
  background: #fff;
  filter: drop-shadow(4px 5px 10px rgba(0, 0, 0, 0.05));
  border-radius: 20px;
  margin-left: 32px;
  flex-shrink: 0;
  &:last-child {
    margin-right: 32px;
  }
`;

export const FucListItemTop = styled.div`
  height: 134px;
  background: url(${bg}) no-repeat center;
  background-size: cover;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
`;
export const FucListItemBottom = styled.div`
  height: 100px;
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  line-height: 100px;
`;

export const MenuBox = styled.div`
  padding-top: 60px;
`;

export const MenuItem = styled.div`
  padding: 0 46px;
  display: flex;
  align-items: center;
  height: 96px;
  margin-bottom: 36px;
  justify-content: space-between;
`;

export const MenuLabel = styled.div`
  display: inline-flex;
  font-weight: bold;
  font-size: 32px;
  align-items: center;
`;

export const MenuIcon = styled.img`
  width: 40px;
  height: 40px;
  margin-right: 90px;
`;

export const MsgNum = styled.div`
  width: 48px;
  height: 48px;
  border-radius: 6px;
  background: ${themeColor};
  font-weight: 500;
  font-size: 14px;
  font-size: 28px;
  line-height: 48px;
  text-align: center;
  color: #fff;
`;
